import React from 'react'
import classnames from 'classnames'
import $ from 'jquery'
import moment from 'moment'

import heartIcon from 'image/love.png'
import moon from 'image/moon.png'
import fireworks from 'image/fireworks.png'
import METEORS from '../../js/meteor'
import XIU from '../../audio/xiu.mp3'
import BGM from '../../audio/envelopBgm.mp3' // 引入背景音乐文件
import style from './envelop.less'
/* eslint-disable */

const TIME = '2019-03-06 19:35:00'
const timePad = str => `${str}`.padStart(2, '0')

class Envelop extends React.Component {
    state = {
        envAnimationType: 'leave',
        conAnimationType: 'enter',
        showEnvelop: false,
        showConfess: false,
        showFire: false,
    }
    interval = -1
    componentDidMount = _ => {
        METEORS.START()
        const pow = new Audio(XIU)
        pow.play()
        setTimeout(() => {
            this.setState({
                envAnimationType: 'enter',
                showEnvelop: true,
            })
            this.getTime()
        }, 500)
        // setTimeout(() => this.bgmAudio.play(), 2000)
    }

    onClickHeart = _ => {
        this.setState({
            envAnimationType: 'leave',
        }, () => {
            setTimeout(() => {
                this.setState({
                    showConfess: true,
                }, () => {
                    this.bgmAudio.play()
                    this.print()
                })
            }, 600)
        })
    }

    closeModal = _ => {
        this.setState({
            showEnvelop: false,
        })
    }

    envAnimationEnd = _ => {
        if (this.state.envAnimationType === 'leave') {
            this.closeModal()
        }
    }

    conAnimationEnd = _ => {
        const { history } = this.props
        if (this.state.conAnimationType === 'leave') {
            history.replace('/fireworks')
        }
    }

    getTime = _ => {
        this.interval = setInterval(() => {
            const diff = moment().diff(moment(TIME))
            const days = moment().diff(moment(TIME), 'days')
            const hours = moment.duration(diff).hours()
            const minutes = moment.duration(diff).minutes()
            const seconds = moment.duration(diff).seconds()
            this.setState({
                days: timePad(days),
                hours: timePad(hours),
                minutes: timePad(minutes),
                seconds: timePad(seconds),
            })
        }, 1000)
    }

    startFire = _ => {
        // 先让文字渐渐消失
        this.setState({
            conAnimationType: 'leave',
        })
    }

    print = () => {
        const that = this
        $.fn.autotype = function () {
            const _this = $(this);
            let str = _this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str = str.replace(/(\s){2,}/g, "$1");
            let index = 0;
            $(this).html('');
            const timer = function fn() {
                const args = arguments;
                const current = str.slice(index, index + 1);
                // html标签完整输出,如：<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length - 1) { //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                    setTimeout(fn, 200)
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                    // 文字结束，出现烟花按钮
                    setTimeout(() => {
                        that.setState({
                            showFire: true,
                        })
                    }, 200)
                };
            };
            // 延迟1s开始
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }

    render() {
        const {
            envAnimationType, conAnimationType, showEnvelop, showConfess, showFire, days, hours, minutes, seconds,
        } = this.state
        return (
            <div className={style.box}>
                <canvas id="meteor" className={style.canvas} />
                <img src={moon} className={style.moon} alt="" />
                {showEnvelop &&
                <div
                    onAnimationEnd={this.envAnimationEnd}
                    className={classnames(style.envelop, style[`envelop-${envAnimationType}`])}
                >
                    <div className={style['triangle-down']} />
                    <img className={style.heart} src={heartIcon} onClick={this.onClickHeart} alt="" />
                    <div className={style['triangle-up']} />
                    <div className={style.text}>你收到一封来信，请点击查收！</div>
                </div>}
                {showConfess &&
                <div className={style['confess-date']}>
                    我们已经一起走过:
                    <span className={style['confess-time']}>{days}</span>天
                    <span className={style['confess-time']}>{hours}</span>小时
                    <span className={style['confess-time']}>{minutes}</span>分钟
                    <span className={style['confess-time']}>{seconds}</span>秒
                </div>}
                {showConfess &&
                <div
                    id="autotype"
                    onAnimationEnd={this.conAnimationEnd}
                    className={classnames(style['confess-main'], style[`confess-${conAnimationType}`])}
                >
                    <h2 className={style['confess-title']}>亲爱的宝宝，</h2>
                    <p>生日快乐🎂！很遗憾不能陪在你身边一起过这个生日，也不能在每个想你的日子拥抱着你。</p>
                    <p>曾经的我忍受过寂寞习惯了孤独，在爱情的世界里一无所知也一无所有，仿若一叶扁舟漫无目的的四处飘游。
                        遇见你之后，才渐渐体会到被人思有人念是一件幸福的事情，不管相隔多远，有你在的地方就是我心❤飞去的方向。</p>
                    <p>在爱情的小站里，你是第一位来客，也会是永远的房主。</p>
                    <p>三生有幸遇见你，人生只有两次幸运就好，一次遇见你，一次走到底。</p>
                    <p className={style.suffix} style={{ paddingTop: 10 }}>Love u forever and ever，by leo</p>
                    <p className={style['suffix-date']}>2019年09月25日</p>
                </div>}
                {showFire && <img src={fireworks} onClick={this.startFire} className={style['confess-fire']} />}
                <audio
                    autoPlay={false}
                    preload="metadata"
                    ref={v => { this.bgmAudio = v }}
                    src={BGM}
                    controls={true}
                    style={{ display: 'none' }}
                    loop={true}
                />
            </div>
        )
    }
}

export default Envelop
